import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import HttpApi from "i18next-http-backend";
import LanguageDetector from "i18next-browser-languagedetector";

i18n
  .use(HttpApi) // Ti d liu t API
  .use(LanguageDetector) // Dng  pht hin ngn ng
  .use(initReactI18next) // React binding
  .init({
    supportedLngs: ["en", "fr"], // Ngn ng c h tr
    fallbackLng: "en", // Ngn ng mc nh
    detection: {
      order: ["querystring", "cookie", "localStorage", "navigator"], // Th t pht hin
      caches: ["cookie"], // Lu ngn ng vo cookie
    },
    backend: {
      loadPath: "https://api.test.soa-dev.net/api/v1/pages?lang={{lng}}", // API endpoint
      parse: (data) => data.data, // Ty chnh parse d liu
    },
    react: { useSuspense: false }, // Tt ch  ch ca React
  });

export default i18n;
